import React from 'react';

import {Flipper, Flipped} from 'react-flip-toolkit';

import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';

import './tag-filter.css';


const styles = theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    padding: theme.spacing.unit / 2
  },
  chip: {
    margin: theme.spacing.unit / 2,
  },
});

// Taken from material-ui ChipArray demo
class TagFilter extends React.PureComponent {
  render() {
    const {classes} = this.props;
    if (this.props.tags.length > 0) {
      return (
	<Paper
	  key='tag-filter'
	  className={[classes.root, 'tag-filter'].join(' ')}>
	  <p>Filter Tags:</p>
	  <div>
	    <Flipper flipKey={this.props.tags.join(' ')}>
	      {this.props.tags.map(tag =>
		<Flipped flipId={tag}>
		<Chip
		  key={tag}
		  label={tag}
		  onDelete={() => this.props.deleteTag(tag)}
		  className={classes.chip}
		/>
		</Flipped>
	      )}
	    </Flipper>
	  </div>
	</Paper>
      );
    } else {
      return null;
    }
  }
}

TagFilter.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(TagFilter);
