import React from 'react';

import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Chip from '@material-ui/core/Chip';

import './card.css';


class ProjectCard extends React.PureComponent {
  constructor(props) {
    super(props);
    this.data = this.props.data;
  }

  renderImage() {
    return (
      <a href={this.data.url}>
	<CardMedia
	  className='project-image'
	  image={this.data.image}
	/>
      </a>
    );
  }

  renderHeader() {
    return (
      <div className='project-header'>
		<div className='project-title'>
			<Typography
			  variant='title'
			  noWrap
			>
			  {this.data.title}
			</Typography>
		</div>
		<div className='project-date'>
			<Typography
			  variant='caption'
			  noWrap
			>
			  {this.data.date}
			</Typography>
		</div>
      </div>
    );
  }

  renderTags() {
    return (this.data.tags.map(tag => 
      <Chip
	key={tag}
	label={tag}
	className='project-tag'
	onClick={(event) => this.props.handleTagClick(event, tag)}
      />
    ));
  }

  renderSocial() {
	  const icons = [];

	  if ('social' in this.data) {
		  for (var site in this.data.social) {
			  const link = this.data.social[site];
			  const image = require('./social-icons/' + site + '.svg');
			  icons.push(
				  <a href={link} key={site}>
				  	<img src={image} alt={site + ' social icon'}/>
				  </a>
			  );
		  }
	  }

	  return icons;
  }

  render() {
    return (
      <Card
	className='project-card'
      >
	{this.renderImage()}
	<CardContent className='project-info-container'>
	  <a href={this.data.url} className='project-info'>
		{this.renderHeader()}
		<p>{this.data.description}</p>
	  </a>

	  <div className='project-footer'>
		  <div className='project-tags-container'>
			<Typography
		      className='project-tag-label'
			  variant='caption'
			>Tags:</Typography>
			<div className='project-tags'>
				{this.renderTags()}
			</div>
		  </div>

		  <div className='project-social'>
			{this.renderSocial()}
		  </div>
	  </div>
	</CardContent>
      </Card>
    );
  }
}

export default ProjectCard;
