import React from 'react';

import ImageBanner from '../../components/ImageBanner/ImageBanner.js';
import bannerImage from './typing-blurred.jpg';

import '../pages.css';
import './contact-page.css';

const contactInfo = {
	phone: '(647) 765-4051',
	email: 'samuel.wgx@gmail.com'
}

class ContactPage extends React.PureComponent {
	renderContact() {
		const items = [];

		for (const channel in contactInfo) {
			const image = require('./icons/' + channel + '.svg');
			items.push(
				<div className='contact-entry'>
					<img src={image} className='contact-icon'/>
					<div className='contact-text'>
						{contactInfo[channel]}
					</div>
				</div>
			);
		}

		return items;
	}

	  render() {
		return (
		  <div id='page-container'>
		<ImageBanner image={bannerImage}>
		  <h1>Contact Me</h1>
		  <p>Want to get in touch? You’re in the right place!</p>
		</ImageBanner>
		<div id='content-container'>
		  <div className='text-container'>
			<p>Got feedback? Have questions? Want to hire me? Just wanna chat?</p>
			<div className='contact-container'>
				{this.renderContact()}
			</div>
			<p>Anything is welcome (...within reason). I will respond to most messages within three business days.</p>
		  </div>
		</div>
		  </div>
		);
	  }
}

export default ContactPage;
