import React from 'react';
import './App.css';

import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import theme from './theme.js';

import NavigationBar from './components/NavigationBar/NavigationBar.js';
import Page from './components/Page.js';

import Projects from './pages/Projects/Page.js';
import About from './pages/About/Page.js';
import Contact from './pages/Contact/Page.js';


const pages = {
  projects: {title: 'Projects', content: <Projects/>},
  about: {title: 'About', content: <About/>},
  contact: {title: 'Contact', content: <Contact/>}
};

const order = ['projects', 'about', 'contact'];
const defaultPage = 'projects';

class App extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      tabIndex: 0
    }
  }

  componentDidMount() {
    this.handlePageChange();
    window.addEventListener('popstate', this.handlePageChange.bind(this));
  }

  handlePageChange() {
    // Load different pages on startup depending on URL
    let page = window.location.pathname.slice(1);
    if (page !== '') {
      if (page.endsWith('.html')) {
	page = page.slice(0, page.length - 5);
      }
      const index = order.indexOf(page);
      if (index !== -1 && this.state.tabIndex !== index) {
	this.handleTabChange(index);
      }
    } else {
      this.handleTabChange(order.indexOf(defaultPage));
    }
  }

  handleTabChange(index) {
    this.setState({tabIndex: index});
    document.title = 'Samuel J. Li — ' + pages[order[index]].title;
  }

  renderNavigationBar() {
    const tabs = order.map(id => ({
	id: id,
	label: pages[id].title
    }));

    return (
      <NavigationBar
	tabs={tabs}
	tabIndex={this.state.tabIndex}
	onTabChange={index => this.handleTabChange(index)}
      />
    );
  }

  renderPage(id) {
    return (<Page key={id}>{pages[id].content}</Page>);
  }

  render() {
    return (
      <MuiThemeProvider theme={theme}>
	{this.renderNavigationBar()}
	{this.renderPage(order[this.state.tabIndex])}
      </MuiThemeProvider>
    );
  }
}

export default App;
